.login-form {
  display: flex;
  flex-direction: column;

  &-logo {
    padding-top: 30%;
  }

  &-web {
    font-size: 48px;
    font-weight: 400;
    color: red;
    padding-top: 20px;
  }

  &-prompt {
    font-size: 20px;
    font-weight: 400;
    color: #3c3c3c;
    padding-top: 60px;
  }

  &-inputs {
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 220px;
  }
}
