.settings-view {
  &-container {
    height: 100vh;
    width: 100%;
    overflow: hidden;
  }

  &-toolbar {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    height: 100px;
    font-size: 20px;
    border-bottom: solid 0.5px #e0e0e0;
    justify-content: center;
  }

  &-options {
    height: 30px;
    width: 150px;
  }

  &-controllers {
    padding: 20px;
  }
}
