.machines-list-item {
  &-name {
    max-width: 200px;
    width: 200px;

    span {
      font-size: 16px;
      font-weight: 500;
      font-family: "sans-serif";
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &-unit-of-measure {
    max-width: 200px;
    width: 200px;

    span {
      font-size: 14px;
      font-weight: 400;
      font-family: "sans-serif";
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
