.login-screen {
  position: fixed;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  background-color: white;
  z-index: 1000000;
  display: flex;
  justify-content: space-between;

  &.small-screen {
    justify-content: center;
  }

  &-left-image {
    height: 100vh;
  }

  &-right-image {
    height: 50vh;
  }
}
